import { VNode, ShapeFlags, VNodeChildAtom } from './vnode'
import { createAppContext, AppContext } from '../seed/createApp'
import { isObject } from '../shared/utils'

export interface Component {}

export interface Data {
  [key: string]: any
}
export interface ComponentOptions {
  setup(
    this: void,
    props: Data,
    context?: Data
  ): Record<string, any> | InternalRenderFunction

  render?: InternalRenderFunction
}

export interface ComponentInstance {
  uid: number
  vnode: VNode | null
  subTree: VNode | null // rendernode
  parent: ComponentInstance | null
  type: ComponentOptions
  root: ComponentInstance | null
  appContext: AppContext | null
  render: InternalRenderFunction | null

  ctx: (Record<string, any> & { _: ComponentInstance }) | null
  // ----- state
  props: Data
  attrs: Data
  setupState: Data
  setupContext: Data | null // setup
  // --proxy
  proxy: object | null
  // --lifecycle
  isMounted: boolean
  isUnmounted: boolean
  isDeactivated: boolean

  // --
  update: Function | null
}

export interface InternalRenderFunction {
  (context: ComponentInstance): VNodeChildAtom
}

let uid = 0
const EMPTY_OBJ = {} // 
export function createComponentInstance(
  vnode: VNode,
  parentComponent: ComponentInstance | null
): ComponentInstance {
  const type = vnode.type as ComponentOptions
  const appContext =
    (parentComponent ? parentComponent.appContext : vnode.appContext) ||
    createAppContext()
  const instance: ComponentInstance = {
    uid: uid++,
    type,
    vnode,
    subTree: null,
    parent: parentComponent,
    root: null,
    appContext,
    render: null,
    setupContext: null,
    ctx: null,

    props: EMPTY_OBJ,
    attrs: EMPTY_OBJ,
    setupState: EMPTY_OBJ,
    proxy: null,
    isMounted: false,
    isUnmounted: false,
    isDeactivated: false,
    update: null,
  }

  instance.root = parentComponent ? parentComponent.root : instance
  instance.ctx = { _: instance } // ,instancectx.
  return instance
}

// component
export function setupComponent(instance: ComponentInstance) {
  const { shapeFlag } = instance.vnode
  const isStateful = shapeFlag & ShapeFlags.STATEFUL_COMPONENT
  //TODO: init props
  console.warn('propstodo')

  // setup
  // setup
  const setupResult = isStateful ? setupStatefulComponent(instance) : undefined

  return setupResult
}
// setup
function setupStatefulComponent(instance: ComponentInstance) {
  const Component = instance.type

  // proxy
  instance.proxy = new Proxy(instance.ctx, {})
  // setup
  const { setup } = Component
  if (setup) {
    // setup2context
    const setupContext = (instance.setupContext = setup.length > 1 ? {} : null)
    // TODO:
    const setupResult = setup(instance.props, setupContext)
    console.debug('setup', setupResult)
    // TODO:setup

    if (typeof setupResult === 'function') {
      // render
      instance.render = setupResult as InternalRenderFunction
    } else if (isObject(setupResult)) {
      instance.setupState = setupResult
    } else {
      console.warn('setup should return object or function,but ', setupResult)
    }
  }
  finishComponentSetup(instance)
}

// setup
// render,
function finishComponentSetup(instance: ComponentInstance) {
  const Component = instance.type as ComponentOptions
  // setuprender
  if (!instance.render) {
    // compile
    instance.render = Component.render || NOOP
  }

  //  
  console.log('==  == todo==')
}

function NOOP() {}
